import React from 'react';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
//import BackGround from '../svg/Vector.svg';
import BackGround from '../svg/finnplus.svg'


const useStyles = makeStyles(theme => ({
  '@global': {
    body: {
      background: '#021E72',
      //backgroundImage: `url(${BackGround})`,
      backgroundRepeat: 'repeat',
    },
  },
  paper: {
    position: 'relative',
    top: '50%',
    background: 'white',
    padding: theme.spacing(4),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    transform: 'translateY(-50%)',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  mainColor: {
    color: '#021E72',
    borderColor: '#021E72',
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
    background: '#021E72',
    boxShadow: 'none',
    color: 'white',
  },
  main: {
    position: 'absolute',
    height: 'auto',
    width: 'auto',
    bottom: 0,
    top: 0,
    left: 0,
    right: 0,
  },
  link: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 'normal',
    fontSize: '12px',
    lineHeight: '16px',
    letterSpacing: '0.4px',
    color: '#021E72',
  }

}));

export default function SignIn() {
  const classes = useStyles();
  /*function handleSubmit (event){
    alert("working");
    event.preventDefault();
  };*/

  return (
    <Container component="main" maxWidth="xs" className={classes.main}>
      <CssBaseline />
      <div className={classes.paper}>
        <Typography component="h1" variant="h5">
          LOG IN
        </Typography>
        <form className={classes.form} method="POST">
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            autoFocus
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
          />
          <FormControlLabel
            control={<Checkbox value="remember" color='primary' />}
            label="Remember me"
          />
          <Grid container>
            <Grid item xs>
              <Link href="/forgotpw" className={classes.link}>
                Forgot password?
              </Link>
            </Grid>
            <Grid item>
              <Link href="/signup" className={classes.link}>
                {"Don't have an account? Register"}
              </Link>
            </Grid>
          </Grid>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            className={classes.submit}
          >
          
            Log In
          </Button>
          <Button
            fullWidth
            variant='outlined'
            className={classes.mainColor}
            href='/'>Cancel
          </Button>

          <input type="hidden" id="signin_token" name="csrf_token" value={window.csrf_token} />
        </form>
      </div>
    </Container>
  );
}