import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";

const useStyles = makeStyles(theme => ({
  payplan: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: '500',
    fontSize: '18px',
    lineHeight: '26px',
    letterSpacing: '1.25px',
    color: 'black',
    '& span' : {
      fontSize: '14px',
      color: theme.palette.text.secondary
    }
  },
  CenterPlanBox: {
    height: '350px',
    borderTop: '1px solid rgba(0, 0, 0, 0.2)',
    borderBottom: '1px solid rgba(0, 0, 0, 0.2)',
    width: '33.3%',
    paddingLeft: '3%',
    paddingRight: '3%',
    position: 'relative',
    background: '#F7F7F7',
  },
  PlanBox: {
    height: '350px',
    border: '1px solid rgba(0, 0, 0, 0.2)',
    width: '33.3%',
    paddingLeft: '3%',
    paddingRight: '3%',
    position: 'relative',
  },
  text: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: '500',
    fontSize: '14px',
    lineHeight: '26px',
    letterSpacing: '1.25px',
    color: theme.palette.text.secondary
  },
  flexContainer: {
    margin: 'auto',
    display: 'flex',
    justifyContent: 'space-evenly'
  },
  subscribe: {
    position: 'absolute',
    bottom: '10%',
    left: '12%',
    background: '#021E72',
    borderRadius: '4px',
    borderStyle: 'solid',
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: '500',
    fontSize: '14px',
    lineHeight: '16px',
    color: 'white',
    textAlign: 'center',
    letterSpacing: '1.25px',
    textTransform: 'uppercase',
    width: '76%',
    height: '14%',
  },
}));

export default function PlanBox(props) {
  const classes = useStyles();
  const data = props.data ? props.data : {
    left: {link: '/signup?method=2', text: 'Subscribe'},
    center: {link: '/signup?method=1', text: 'Subscribe'},
    right: {link: '/signup?method=0', text: 'Subscribe'},
  };

  return (
    <Grid item className={classes.flexContainer}>
      <div id='left' className={classes.PlanBox}>
        <p className={classes.payplan}>SINGLE PAYMENT</p>
        <p className={classes.payplan}>50 cents <span>(per article)</span></p>
        <p className={classes.text}>- Access articles from all online Finnish newspapers</p>
        <p className={classes.text}>- Read on any device</p>
        <Button href={data.left.link} className={classes.subscribe}>
             {data.left.text}
        </Button>
      </div>
      <div id='center' className={classes.CenterPlanBox}>
        <p className={classes.payplan}>PACKAGE SUBSCRIPTION</p>
        <p className={classes.payplan}>5€ <span>(per month)</span></p>
        <p className={classes.text}>- Access 15 articles from all online Finnish newspapers</p>
        <p className={classes.text}>- Read on any device</p>
        <p className={classes.text}>- Cancel at anytime</p>
        <Button href={data.center.link} className={classes.subscribe}>
             {data.center.text}
        </Button>
      </div>
      <div id='right' className={classes.PlanBox}>
        <p className={classes.payplan}>MONTHLY SUBSCRIPTION</p>
        <p className={classes.payplan}>5€ <span>(per month)</span></p>
        <p className={classes.text}>- Unlimited access to all online Finnish newspapers</p>
        <p className={classes.text}>- Read on any device</p>
        <p className={classes.text}>- Cancel at anytime</p>
        <Button href={data.right.link} className={classes.subscribe}>
             {data.right.text}
        </Button>
      </div>
    </Grid>
  )
}