import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from "@material-ui/core/Grid";
import { ReactComponent as FinnplusLogo} from "../../svg/finnpluswhite.svg";
import Link from "@material-ui/core/Link";

const useStyles = makeStyles(theme => ({
  root: {
    background: '#2E2E2E',
  },
  item: {
    textAlign: 'center',
  },
  container: {
    padding: theme.spacing(4)
  },
  '@global': {
    p: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 'normal',
      fontSize: '14px',
      lineHeight: '24px',
      letterSpacing: '0.1px',
      color: 'white',
    },
    h4: {
      fontFamily: 'Roboto',
      fontStyle: 'normal',
      fontWeight: 500,
      fontSize: '16px',
      lineHeight: '24px',
      letterSpacing: '0.1px',
      color: 'white',
    }
  }
}));


export default function BottomBar() {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Grid container justify='center' className={classes.container}>
        <Grid xs={2} className={classes.item}>
          <h4>About FiNN+</h4>
          <Link href='/'><p>Privacy Policy</p></Link>
          <Link href='/'><p>Terms of Conditions</p></Link>
          <Link href='/'><p>Cookies</p></Link>
        </Grid>
        <Grid xs={2} className={classes.item}>
          <h4>Subscription Plans</h4>
          <Link href='/'><p>Single Payment</p></Link>
          <Link href='/'><p>Package Subscription</p></Link>
          <Link href='/'><p>Monthly Subscription</p></Link>
        </Grid>
        <Grid xs={2} className={classes.item}>
          <FinnplusLogo/>
        </Grid>
        <Grid xs={2} className={classes.item}>
          <h4>Customer Service</h4>
          <Link href='/'><p>Register as a Publisher</p></Link>
          <Link href='/'><p>FAQS</p></Link>
        </Grid>
        <Grid xs={2} className={classes.item}>
          <h4>Email newsletter</h4>
          <p>Latest FiNN+ news</p>
          <p>Latest FiNN+ updates</p>
        </Grid>
      </Grid>
    </div>
  )
}