import React ,{useEffect} from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import HomeIcon from '@material-ui/icons/Home';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Checkbox from '@material-ui/core/Checkbox';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from "@material-ui/core/FormControl";
import IconButton from '@material-ui/core/IconButton';
import PhotoIcon from '@material-ui/icons/Face';

import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import TestObj from '../../test-data/testObj'

const useStyles = makeStyles(theme => ({
  '@global': {
    body: {
      background: '#021E72',
    },
  },
  main: {
    position: 'absolute',
    height: 'auto',
    width: 'auto',
    bottom: 0,
    top: 0,
    left: 0,
    right: 0,
  },
  paper: {
    position: 'relative',
    top: '50%',
    padding: theme.spacing(4),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    background: 'white',
    transform: 'translateY(-50%)',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    color: 'white',
    background: '#021E72',
  },
  button: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    color: '#021E72',
  },
  formControl: {
    margin: theme.spacing(1),
    width: '100%',
  },
  input: {
    display: 'none',
  },
  fileUpload: {
    width: '100%',
  },
}));

let myObj = TestObj.user ;

  if(window.obj !== undefined)
    myObj = window.obj.user;


export default function ProfileEdit() {
  const [state, setState] = React.useState({
    matched: false ,
    password:'',
    rPassword:'',
    
  });

  const handleChange = name => event => {
    setState({ ...state, [name]: event.target.value });
    
    //console.log(`method = ${state.method}, href=${window.location.search}`);
  };

  const handleMatch = name => event => {
    setState({ ...state, [name]: event.target.value});
    
    //console.log(`password = ${state.password} ,rPassword= ${state.rPassword} ,matched= ${state.matched}`)
  };
//get the chosen subscription type from the frontpage

useEffect(() => {
    // checking the password and the confirm password all the time.
    if(state.password === state.rPassword)
      setState({ ...state, matched: true});
    else 
      setState({ ...state, matched: false});
},[state]);



  const classes = useStyles();

  return (
    <Container component="main" maxWidth="xs" className={classes.main}>
      <CssBaseline />
      <div className={classes.paper}>
        <Typography component="h1" variant="h5">
          Edit your information
        </Typography>
        <form className={classes.form} method="POST" encType="multipart/form-data">
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <TextField
                autoComplete="fname"
                name="firstName"
                variant="outlined"
                
                fullWidth
                id="firstName"
                label="First Name"
                autoFocus
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                
                fullWidth
                id="lastName"
                label="Last Name"
                name="lastName"
                autoComplete="lname"
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                
                fullWidth
                id="email"
                label="Email Address"
                name="email"
                autoComplete="email"
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                
                fullWidth
                value={state.password}
                name="password"
                label="New Password"
                type="password"
                id="password"
                onChange={handleMatch('password')}
              />
            </Grid>
            <Grid item xs={12}>
             
              <TextField
                error= {!state.matched}
                variant="outlined"
                
                fullWidth
                value={state.rPassword}
                name="rPassword"
                label="Repeat Password"
                type="password"
                id="rPassword" 
                onChange={handleMatch('rPassword')}
              />
             
              {!state.matched &&<FormHelperText>password not matched </FormHelperText>}
            </Grid>
            <Grid item xs={12}>
              <Input  id="photo-file" name="photo-file" type="file" className={classes.fileUpload}/>
            </Grid>
          </Grid>
          
          <Button

            type="submit"
            fullWidth
            variant="contained"
            className={classes.submit}
          >
            Save
          </Button>
          <Button  fullWidth variant="outlined" href="/profile">
            cancel
          </Button>
          <input type="hidden" id="edit_token" name="csrf_token" value={window.csrf_token} />
        </form>
      </div>
      
    </Container>
  );
}