import React from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import BottomBar from "../Landingpage/bottombar";
import PlanBox from "./planbox";
import ProfileCard from "../Dashboard/ProfileCard";
import Popover from "@material-ui/core/Popover";
import IconButton from "@material-ui/core/IconButton";
import {AccountCircle} from "@material-ui/icons";
import { ReactComponent as FinnplusLogo} from "../../svg/finnplus.svg";
import Link from "@material-ui/core/Link";


const useStyles = makeStyles(theme => ({
  root: {
    minHeight: '100vh',
    flexGrow: 1
  },
  header: {
    display: 'flex',
    justifyContent: 'space-between',
    padding: theme.spacing(4),
    paddingTop: theme.spacing(1),
  },
  right: {
    alignSelf: 'flex-end',
  },
  profileCardContainer: {
    width: '370px'
  },
  logo: {
    padding: '6px 8px',
    textAlign: 'center',
    marginLeft: '3%',
  },
  main: {
    maxWidth: '1200px',
    padding: theme.spacing(4),
    margin: 'auto',
  },
  headline: {
    margin: 'auto',
    padding: theme.spacing(6),
    textAlign: 'center',
  },
  bottom: {
    position: 'absolute',
    bottom: 0,
    width: '100%',
  },
  text: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 300,
    fontSize: '12px',
    fontHeight: '16px',
    letterSpacing: '1.25px',
    color: 'black'
  },
  textRight: {
    textAlign: 'right'
  },
  textCenter: {
    textAlign: 'center'
  },
}));

export default function Subscription() {
  const classes = useStyles();
  const theme = useTheme();
  const userData = window.obj ? window.obj.user : {name: 'User Name', email: 'username@gmail.com', plan: 'Single payment, 4 articles left', image:'https://material-ui.com/static/images/avatar/1.jpg'};
  const [anchorEl, setAnchorEl] = React.useState(null);
  const data = {
    left: {link: '/payform?method=2', text: userData.subscription_end ? 'SELECT' : userData.package_end ? 'SELECT' : userData.tokens ? 'TOP UP' : 'UPGRADE'},
    center: {link: '/payform?method=1', text: userData.subscription_end ? 'SELECT' : userData.package_end ? 'TOP UP' : userData.tokens ? 'UPGRADE' : 'UPGRADE'},
    right: {link: '/payform?method=0', text: userData.subscription_end ? 'TOP UP' : userData.package_end ? 'UPGRADE' : userData.tokens ? 'UPGRADE' : 'UPGRADE'}
  };

    const handleProfileMenuOpen = event => {
    console.log('open');
    setAnchorEl(event.currentTarget);
  };


  const handleClose = () => {
    setAnchorEl(null);
  };

  const profileCardOpen = Boolean(anchorEl);
  const id = profileCardOpen ? 'simple-popover' : undefined;

  return (
    <div className={classes.root}>
      <header className={classes.header}>
        <Link href='/dashboard'>
        <FinnplusLogo className={classes.logo}/>
        </Link>
        <div className={classes.right}>
          <IconButton
            edge="end"
            aria-label="account of current user"
            aria-haspopup="true"
            onClick={handleProfileMenuOpen}
            color="inherit"
          >
            <AccountCircle />
          </IconButton>
          <Popover
            id={id}
            open={profileCardOpen}
            anchorEl={anchorEl}
            onClose={handleClose}
            anchorOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            className={classes.popover}
          >
            <div className={classes.profileCardContainer}/>
            <ProfileCard data={userData}/>
          </Popover>
        </div>
      </header>
      <main className={classes.main}>
        <h1 className={classes.headline}>RENEW YOUR PLAN</h1>
        {
          userData.subscription_end ? <p className={clsx(classes.text, classes.textRight)}>Your current plan - Monthly subscription ends {userData.subscription_end}</p> :
          userData.package_end ? <p className={clsx(classes.text, classes.textCenter)}>Your current plan - Package subscription, {userData.package_end} articles left </p> :
          userData.tokens ? <p className={clsx(classes.text)}>Your current plan - Single payment, {userData.tokens} articles left </p> :
          <p className={clsx(classes.text, classes.textCenter)}>No active plan</p>
        }
        <PlanBox data={data}/>
      </main>
      <div className={classes.bottom}>
        <BottomBar/>
      </div>
    </div>
  )
}